import React, { Component } from 'react';

import * as ReactBootStrap from "react-bootstrap";

import { TabArray } from './tab-array';

export class TabLeft extends Component {

    render() {

        return (
            TabArray.map((itemTab) =>
                <ReactBootStrap.ListGroup>
                    <ReactBootStrap.Nav.Link className="list-group-item list-group-item-action" data-toggle="list" eventKey={itemTab.eventKey}>{itemTab.name}</ReactBootStrap.Nav.Link>
                </ReactBootStrap.ListGroup>
            )
        )
    }
}